import patternErrorMessage from "../helpers/patternErrorMessage";

const errorMsg:any = {
  required: () => `El campo es requerido.`,
  minlength: ({ minlength }:any) => `Campo debe ser mínimo ${minlength.requiredLength} caracteres.`,
  maxlength: ({ maxlength }:any) => `Campo debe ser máximo ${maxlength.requiredLength} caracteres.`,
  email: () => `Correo electrónico inválido.`,
  notEqual: () => `Los campos ingresados no coinciden`
}

function getMessage(error:any) : string {
  let keyName = Object.keys(error)[0];
  if(keyName === 'pattern')
    return patternErrorMessage[error.pattern.requiredPattern]
  
  return errorMsg[keyName](error);
}

function ctrlErrorMsg(ctrl:any) : string {
  const hasError = ctrl?.touched && !ctrl?.valid
  if(!hasError) return ''
  if(!ctrl.errors) return ''
  return getMessage(ctrl?.errors)
}

export {
  ctrlErrorMsg
}