import { Component, Input } from '@angular/core';

@Component({
  selector: 'fn-data-field',
  template: `
    <div class="field">
      <label class="text-color-secondary">{{label}}</label>
      <div>{{text}}</div>
    </div>
  `,
})
export class FnDataFieldComponent {
  @Input() label: string = 'Label';
  @Input() text: string = 'Texto Informativo';
}
