import { Component, Input } from '@angular/core';

@Component({
  selector: 'fn-table-btn',
  template: `
    <button pButton pRipple class="p-button-rounded p-button-outlined font-semibold text-primary" [ngClass]="{'btn-shading': shading}">
      <fn-icon *ngIf="ico" [ico]="ico" height="1.25em" class="mr-2" />
      {{text}}
    </button>
  `,
  styles: [
    'button {border: 2px solid !important;}',
    '.btn-shading {background-color: var(--surface-overlay);}'
  ]
})
export class FnTableBtnComponent {
  @Input() text: string = 'Button description';
  @Input() shading: boolean = false;
  @Input() ico: any = null;
}
