import { Component, Input } from '@angular/core';

@Component({
  selector: 'fn-txt-logo',
  template: `
    <div class="txt-logo font-bold text-5xl bg-white inline-block px-1">
      <span class="firstLetter">{{first}}</span>
      <span class="secondLetter">{{second}}</span>
    </div>
  `,
  styles: [
    '.txt-logo {text-transform: uppercase; border-radius: 0 0 12px 12px; line-height:1.2;}',
    '.firstLetter {letter-spacing: -0.6rem; color: #0E2E4A}',
    '.secondLetter {color: #F19700}'
  ]
})
export class FnTxtLogoComponent {
  @Input() first: string = 'b'
  @Input() second: string = 'f'
}
