import { Component, HostListener, Input, OnInit } from '@angular/core';

type OrientationType = 'horizontal' | 'vertical';

@Component({
  selector: 'fn-timeline',
  templateUrl: './fn-timeline.component.html',
  styleUrls: ['./fn-timeline.component.scss']
})
export class FnTimelineComponent implements OnInit{

  @Input() items: string[] = []
  @Input() currentIndex: number = 0
  @Input() orientation: OrientationType = 'horizontal'
  @Input() maxWidth: number = 820

  public currentOrientation: OrientationType = 'horizontal'

  constructor(){
    this.currentOrientation = this.orientation
  }

  ngOnInit(): void {
    this.checkWindowSize()
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: Event): void {
    this.checkWindowSize();
  }

  private checkWindowSize(): void {
    this.currentOrientation = window.innerWidth <= this.maxWidth  ? 'vertical' : this.orientation;
  }

}