import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, Optional, Output, Self, ViewChild, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, NgControl } from '@angular/forms';
//util
import { ctrlErrorMsg } from '../../shared/utils/input-error';

@Component({
  selector: 'fn-input',
  templateUrl: './fn-input.component.html',
  providers: [
    // {
    //   provide: NG_VALUE_ACCESSOR,
    //   useExisting: forwardRef(() => FnInputComponent),
    //   multi: true
    // }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FnInputComponent implements ControlValueAccessor {

  @Input() public type: "text" | "password" =  "text";
  @Input() public label: string = "";
  @Input() public ico: any = null;
  @Input() public placeholder: string = "";
  @Input() public disabled: boolean = false;
  @Input() public styleClass: string = "";
  //
  @Input() public checking: boolean = false;
  @Input() public counter: number = 0;

  @Output() onBlur: EventEmitter<any> = new EventEmitter();

  @ViewChild('input') input!: ElementRef

  public value: string = '';

  public onModelChange: Function = () => {}
  public onModelTouched: Function = () => {};

  constructor(
    private cd: ChangeDetectorRef,
    @Optional() @Self() public ngControl: NgControl
  ) {
    if(this.ngControl != null)
      this.ngControl.valueAccessor = this
  }

  get errorMsg() {
    return ctrlErrorMsg(this.ngControl?.control)
  }

  onInput(event: any) {
    this.value = event.target.value;
    this.onModelChange(this.value);
  }

  onInputBlur(event: Event) {
    this.onModelTouched();
    this.onBlur.emit(event);
  }

  writeValue(value: any): void {
    if (value === undefined) this.value = '';
    else this.value = value;

    this.cd.markForCheck();
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

}
